<template>
  <div>
    <gv-header v-bind:title="msg"></gv-header>
    <a class="big" href="#/create">
      <div class=" create material-icons">create</div>
      <div class="text">TENHO UMA IDEIA</div>
    </a>

    <br />
    <h1>MINHAS IDEIAS</h1>
    <gv-padlist></gv-padlist>
    </div>
</template>

<script>

export default {
  name: 'Panel',
  data () {
    return {
      msg: 'IdeaPad'
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

h1{
  color: black;
  border-bottom: 3px solid black;
  padding: 20px;
  margin: 20px;
}

.big{
  text-align: center;
  display: block;
  color: black;
  text-decoration: none;
  padding: 125px 0px 60px;
  margin: 0px;
  border-radius: 5px;
}

.big .create{
  font-size:60px;
  display: block;
}

.big .text{
  font-size: 18px;
  width: 200px;
  display: inline-block;
  background: black;
  color: white;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
}

.idea{
  text-align: left;
  border-bottom: 2px solid rgb(20, 20, 20);
  margin: 10px;
  border-radius: 5px;
  font-size: 20px;
}

.message{
  padding: 24px;
  background: rgb(240, 240, 240);
}

.button{
  padding: 5px 25px;
  display: inline-block;
  background: rgb(220, 220, 220);
  border-top: 2px solid rgb(20, 20, 20);
  color: black;
  text-decoration: none;
}

</style>



