<template>
    <div>
        <div v-if="this.$root.padList.length > 0 ? 0 : 1">
            <h3>You doesn't have pad's</h3>
        </div>
        <div v-else>
            <div class="idea" v-bind:key="index" v-for="(idea, index) in this.$root.padList">
            <div class="edit">
                <router-link  class="button" :to="{ name: 'Edit', params:{id:idea.id} }">
                Edit
                </router-link>
                <div v-on:click="deletePad(idea.id, idea.message)" class="button">
                Delete
                </div>
            </div>
            <div class="message">
                {{idea.message}}
            </div>
        </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'PadList',
  data () {
    this.$root.load()

    return {
      msg: 'IdeaPad'
    }
  },

  methods: {
    deletePad: function (id, text) {
      var desejaRemover = confirm('Do you want to remove the pad "' + text + '" ')

      if (desejaRemover) {
        localStorage.removeItem('idea-' + id)
        alert('Pad deleted')
        this.$root.load()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.big{
  text-align: center;
  display: block;
  color: black;
  text-decoration: none;
  padding: 55px;
  background: rgb(240, 240, 240);
  border-bottom: 2px solid black;
  margin: 10px;
  border-radius: 5px;
}

.big .create{
  font-size:60px;
  display: block;
}

.big .text{
  font-size: 28px;
  width: 200px;
  display: inline-block;
}

.idea{
  text-align: left;
  border-bottom: 2px solid rgb(20, 20, 20);
  margin: 10px;
  border-radius: 5px;
  font-size: 20px;
}

.message{
  padding: 24px;
  background: rgb(240, 240, 240);
}

.button{
  padding: 5px 25px;
  display: inline-block;
  background: rgb(220, 220, 220);
  border-top: 2px solid rgb(20, 20, 20);
  color: black;
  text-decoration: none;
}

</style>



