<template>
    <div>
        <header>
            <h1><span class="yellow">e</span>DEIAS</h1>
        </header>
        <div class="fill"></div>
    </div>
</template>

<script>

export default {
  name: 'Header',
  props: ['title']
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

header{
    position: fixed;
    width: 100%;
    height: 70px;
    color: white;
    background: black;
}

.yellow{
    color: rgb(250, 200, 0);
}

h1{
    padding: 9px 0 9px;
    margin: 0;
    color: white;
    font-size: 39px;
}

.fill{
    height: 50px;
    display: block;
}

</style>



