// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false
Vue.prototype.$pads = []
Vue.prototype.$update = 0

var app = {
  data: function () {
    return {
      padList: []
    }
  },

  methods: {
    load: function () {
      var total = Number(localStorage.getItem('count'))
      this.$root.padList = []

      for (var i = total; i > 0; i--) {
        if (localStorage.getItem('idea-' + i)) {
          this.$root.padList.push({
            'id': i,
            'message': localStorage.getItem('idea-' + i)
          })
        }
      }
    }
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  mixins: [app],
  router,
  components: { App },
  template: '<App/>'
})



