<template>
  <div>
  <gv-header v-bind:title="msg"></gv-header>
  <textarea id="idea" placeholder="Write here" class="textarea" v-model="ideaContent"></textarea>
  <button v-on:click="storeIdea" class="btn">GUARDAR</button>
  </div>
</template>

<script>

export default {
  name: 'Painel',
  data () {
    var id = (this.$route.params.id)
    return {
      msg: 'IdeaPad',
      ideaContent: localStorage.getItem('idea-' + id)
    }
  },

  methods: {
    storeIdea: function (event) {
      localStorage.setItem('idea-' + this.$route.params.id, document.getElementById('idea').value)
      this.$root.load()
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>

.btn{
    background:rgb(20, 20, 20);
    font-family: Arial Black, Helvetica, sans-serif;
    color: white;
    padding: 10px 20px;
    font-size: 24px;
    border: none;
    border-radius: 5px;
}

.textarea{
    display: inline-block;
    margin: 70px 30px 50px;
    width: 70%;
    border: none;
    height: 200px;
    font-family: Arial Black, Helvetica, sans-serif;
    border-bottom: 2px solid black;
    padding: 25px;
    font-size: 30px;
    outline: none;
}

</style>



